<template>

<div id="itemView">


  <loading-cover v-show="isLoading" transition="fade">
  </loading-cover>

  <dark-cover v-show="showSelect" transition="fade" v-on:click.self="dismissItemSelect">
  </dark-cover>

  <item-select v-if="showSelect" :item="item" transition="slideUp">
  </item-select>

  <div class="item-view" style="margin-top: -60px;">

    <carousel :indicators="true" :auto="3000" :responsive="100" :loop="true">
      <carousel-item v-for="photo in item.photos">
        <img :src="photo.image_url">
      </carousel-item>
    </carousel>


    <section>
  
      <header class="item-view header">
        <h4>{{ item.title }}</h4>
        <p>
          <span class="price">{{ item.price_label }}</span>
        </p>

      </header>

      <div class="button outline success" v-on:click.self="addToCart"></div>

      <hr class="cell" />

      <div v-if="item.sizes && (item.sizes.length > 0)">
        <article class="flex five">
          <div class="fifth"></div>

          <div class="four-fifth">
            <span v-for="size in item.sizes" class="label">
              {{ size.label }}
            </span>
          </div>
        </article>
        <hr class="cell" />
      </div>

      <div v-if="item.colors && (item.colors.length > 0)">
        <article class="flex five">
          <div class="fifth"></div>

          <div class="four-fifth">
            <span v-for="color in item.colors" class="label">
              {{ color.label }}
            </span>
          </div>
        </article>
        <hr class="cell" />
      </div>

      <article>
        <h5></h5>
        <p class="small" style="color: #999;">{{ item.desc }}</p>

        <h5></h5>
        <p class="small" style="color: #999;">{{ item.item_footer }}</p>
      </article>

      <hr/>
      <p class="fancy subtitle"><span class=" small mute"></span></p>
      <p><!-- end, to skip past :last-child style --></p>

      <article class="item-view detail-photos">
        <div v-for="photo in item.detail_photos" class="full-width">
          <img :src="photo.image_url" /></div>
        </div>
      </article>

    </section>  

    <hr style="margin-bottom: 50px;" />

  </div>
</div>
</template>

<script>
import { Carousel, CarouselItem } from 'vue-m-carousel'
import LoadingCover from './LoadingCover.vue'
import DarkCover from './DarkCover.vue'
import ItemSelect from './ItemSelect.vue'

import Vue from 'vue'

// custom transition
Vue.transition('slideUp', {
  enterClass: 'slideUp-enter',
  leaveClass: 'slideDown-leave'
})

export default {
  name: 'ItemView',

  data () {
    return {
      item: {},
      isLoading: true,
      showSelect: false
    }
  }, 

  components: {
    Carousel,
    CarouselItem, 
    ItemSelect,
    LoadingCover,
    DarkCover
  },

  ready() {
    console.log("[ItemView ready] loading");
  }, 

  route: {
    data ({ to }) {
      // This is the route data hook. It gets called every time the route
      // changes while this component is active.
      // 
      // What we are doing:
      // 
      // 1. Get the `to` route using ES2015 argument destructuring;
      // 2. Get the `page` param and cast it to a Number;
      // 3. Fetch the items from the store, which returns a Promise containing
      //    the fetched items;
      // 4. Chain the Promise and return the final data for the component.
      //    Note we are waiting until the items are resolved before resolving
      //    the entire object, because we don't want to update the page before
      //    the items are fetched.
      var itemId = to.params.id
      document.title = 'Lion IQ Item Detail View'

      // fetch item from API
      this.getItem(itemId)
      console.log("[ItemView route] params: " + itemId)
    },

    deactivate (transition) {
      // reset state
      this.$set('isLoading', true)
      this.$set('showSelect', false)

      transition.next()
    }
  },

  methods: {

    addToCart() {
      this.$set('showSelect', true)
    }, 

    dismissItemSelect() {
      this.$set('showSelect', false)
    },

    getItem (itemId) {
      var vm = this;

      // request callback
      var callback = function(response) {
          console.log("[ItemView callback] response: " + JSON.stringify(response.body))
          
          var item = response.json()
          vm.$set('item', item)

          vm.$set('isLoading', false)

          // change document title too
          document.title = this.item.title + ' | Lion IQ'
      }

      var errorCallback = function(response) {
          console.log("[error callback] response: " + response.json());
      }

      // request url 
      var url = "http://lioniq.dev/api/items/" + itemId;

      // request headers
      var headers = {
          "Content-Type": "application/json",
          "Accept": "application/json",
          "X-APP-KEY": APP_KEY,
          "X-APP-SECRET": APP_SECRET
      };

      // vue-resource
      this.$http.get(url, {headers: headers}).then(callback, errorCallback);
    }, 

  }

}
</script>


<style>

    /* fancy before/after horizontal line decoration*/
    .subtitle {
      margin: 2em 0;
    }
    .fancy {
      line-height: 0.5;
      text-align: center;
    }
    .fancy span {
      display: inline-block;
      position: relative;  
    }
    .fancy span:before,
    .fancy span:after {
      content: "";
      position: absolute;
      height: 5px;
      /*border-bottom: 1px solid #DDD;*/
      border-top: 1px solid #DDD;
      top: 2.5px;
      /*width: 600px;*/
      width: 50%;
    }
    .fancy span:before {
      right: 100%;
      margin-right: 15px;
    }
    .fancy span:after {
      left: 100%;
      margin-left: 15px;
    }

    /* carousel img styling */
    .carousel-item img {
      width: 100%;
    }

    /* carousel dot styling */
    .item-view .carousel-dot {
      width: 12px; height: 12px;
      border-radius: 6px;
      font-size: 0;
    }
    .item-view .carousel-dot.active {
      background-color: #ccc;
    }
</style>


