<template>

<div id="itemSelect">

  <section>
    <div class="pull-left" style="margin-top: -20px; margin-right: 20px;">
      <img :src="item.square_cover_photo_url" style="width: 60px; height: 60px;"/>
    </div>

    <div class="pull-left">
      <div class="clearfix">
        <h5 class="price">{{ item.price_label }}</h5>
        <p></p>
      </div>
    </div>

    <div class="pull-right">
      <div class="mute small button pseudo" v-on:click.self="dismiss">[x]</div>
    </div>

    <div class="clearfix"></div>
  </section>

  <hr/>

  <section v-if="item.sizes && (item.sizes.length > 0) ">
    <article class="flex five">
      <div class="fifth"></div>

      <div class="four-fifth">
        <div v-for="size in item.sizes" 
          class="size small skew button" 
          :class="{'outline': (size != pickedSize)}"
          v-on:click.self="pickSize(size)">

          {{ size.label }}
        </div>
      </div>
    </article>
    <hr class="cell" />
  </section>


  <section v-if="item.colors && (item.colors.length > 0)">
    <article class="flex five">
      <div class="fifth"></div>

      <div class="four-fifth">
        <div v-for="color in item.colors" 
          class="color small skew button outline" 
          :class="{'outline': (color != pickedColor)}"
          v-on:click.self="pickColor(color)">

          {{ color.label }}
        </div>
      </div>
    </article>
    <hr class="cell" />
  </section>

  <section>
    <article class="flex five">
      <div class="fifth"></div>
      <div class="four-fifth">
        <qty-input :qty="1"></qty-input>
      </div>
    </article>
    
  </section>

  <section style="position: fixed; bottom: 10px; height: 44px;">
    <div class="button buy success" style="width: 100%;" @click="addToCart"></div>
  </section>
</div>
</template>

<script>

import QtyInput from './QtyInput.vue'

export default {
  name: 'ItemSelectView',

  props: {
    item: Object,

    pickedSize: Object,

    pickedColor: Object, 
  },

  data () {
    return {
      qty: 1,
    }
  }, 

  components: {
    QtyInput
  }, 

  methods: {

    pickSize(size) {
      console.log("[ItemSelectView pickSize] size: " + size.label)
      this.$set("pickedSize", size)
    },

    pickColor(color) {
      console.log("[ItemSelectView pickColor] color: " + color.label)
      this.$set("pickedColor", color)
    },

    addToCart () {

      var cartItem = {
        item: this.item,
        itemKey : this.item.key,
        qty: this.qty
      }

      // any size option?
      if (typeof this.pickedSize !== 'undefined') {
        cartItem.size = this.pickedSize
        cartItem.sizeLabel = this.pickedSize.label
      }

      // any color option?
      if (typeof this.pickedColor !== 'undefined') {
        cartItem.color = this.pickedColor
        cartItem.colorLabel = this.pickedColor.label
      }

      console.log("[ItemSelectView addToCart] " + JSON.stringify(cartItem))

    }, 

    dismiss() {
      this.$parent.dismissItemSelect()
    }
  }

}
</script>


<style>
  #itemSelect {
    height: 80%;
    margin-top: 30%;
    width: 100%;
    background-color: white;
    position: fixed;
    z-index: 400; /* more than 300 */
  }
  .skew.button {
    min-width: 44px;
    margin: 0 10px;
  }
</style>



