<template>
  <article>
    <a href="#/items/{{ item.key }}">
      <div class="full-width"><img :src="item.square_cover_photo_url" /></div>
      <h4>{{ item.title }}</h4>
      <p><span class="price">{{ item.price_label }}</span></p>
    </a>
  </article>
</template>

<script>
export default {
  name: 'Item',

  props: {
    item: Object
  }, 
}
</script>



