<template>

  <div id="shop-wrapper">

    <banners :banners="banners"></banners>

    <section>
      <items-grid :items="items"></items-grid>
    </section>

    <hr/>
    <section>
      <h4>Shopping Cart</h4>
      <ul>
          <li v-for="cartItem in cartItems" track-by="$index">
          <b>{{ cartItem.title }}</b> ${{ cartItem.price }}
          </li>
      </ul>
      <p>total: {{ totalAmount }}</p>
    </section>
  </div>
</template>

<script>
import ItemsGrid from './ItemsGrid.vue'
import Banners from './Banners.vue'

export default {
  name: 'ShopView',

  props: {
      totalAmount: Number,
      cartItems: Array  
  }, 
  components: {
    ItemsGrid, 
    Banners
  }, 
  ready() {
    this.cartItems = []
    this.totalAmount = 0
    
    this.getItems()
    this.getBanners()
    console.log("[ShopView] ready");
    console.log("[ShopView] APP_KEY: " + APP_KEY + ", APP_SECRET: " + APP_SECRET);
  }, 
  data() {
    return {
      items: [], 
      banners: []
    }
  }, 
  methods: {
    getItems () {
      var vm = this;

      // request callback
      var callback = function(response) {
          console.log("[getItems callback] response: " + JSON.stringify(response.body));
          vm.$set('items', response.json());
      }

      var errorCallback = function(response) {
          console.log("[error callback] response: " + response.json());
      }

      // request url 
      var url = "http://lioniq.dev/api/items";

      // request headers
      var headers = {
          "Content-Type": "application/json",
          "Accept": "application/json",
          "X-APP-KEY": APP_KEY,
          "X-APP-SECRET": APP_SECRET
      };

      // vue-resource
      this.$http.get(url, {headers: headers}).then(callback, errorCallback);
    }, 

    getBanners () {
      var vm = this;

      // request callback
      var callback = function(response) {
          console.log("[getBanners callback] response: " + JSON.stringify(response.body));
          vm.$set('banners', response.json());
      }

      var errorCallback = function(response) {
          console.log("[getBanners error callback] response: " + response.json());
      }

      // request url 
      var url = "http://lioniq.dev/api/banners";

      // request headers
      var headers = {
          "Content-Type": "application/json",
          "Accept": "application/json",
          "X-APP-KEY": APP_KEY,
          "X-APP-SECRET": APP_SECRET
      };

      // vue-resource
      this.$http.get(url, {headers: headers}).then(callback, errorCallback);
    }
  }
}
</script>



